import React, {useEffect, useState} from 'react';
import '../css/doughnut.css';
import { songs } from '../utils/Notes.js';
import { getDoughnutStyle } from "../utils/Funcs";

const Doughnut = ({ currentNoteIndex, animate }) => {

    const [isAnimating, setIsAnimating] = useState(false);
    const notes = songs.clockwise;
    const notePositions = {
        C: {
            x: 47.6,
            y: 53.5
        },
        G: {
            x: 47,
            y: 53.5
        },
        D: {
            x: 46.6,
            y: 52.8
        },
        A: {
            x: 46.4,
            y: 52.3
        },
        E: {
            x: 47.6,
            y: 52.4
        },
        B: {
            x: 47.6,
            y: 52
        },
        Gb: {
            x: 45,
            y: 51.6
        },
        Db: {
            x: 45.5,
            y: 51.6
        },
        Ab: {
            x: 46,
            y: 51.6
        },
        Eb: {
            x: 47,
            y: 52
        },
        Bb: {
            x: 46.5,
            y: 52.5
        },
        F: {
            x: 47.6,
            y: 53.5
        }
    }

    useEffect(() => {
        setIsAnimating(true);
    }, []);

    const getSvg = () => {
        return (
            <svg viewBox="0 0 100 100" width='100%'>
                {notes.map((note, index) => {
                    let fill = '#ffe135';
                    const styles = getDoughnutStyle(currentNoteIndex, isAnimating, index)
                    if (index === currentNoteIndex) {
                        fill = '#ff0000';
                    }

                    let displayText = notes[index]
                    if (displayText.length === 2) {
                        displayText = displayText[0] + '\u266D';
                    }
                    return (
                        // eslint-disable-next-line
                        <a onClick={() => console.log("clicked")}
                           key={index}
                           href="#"
                        >
                            <g style={styles[0]}>
                                <path
                                    key={index}
                                    d="M41,50
                                       A31,31 0 0,1 59,50
                                       L56.5,60
                                       A35,35 0 0,0 43.5,60
                                       Z"
                                    fill={fill}
                                />
                            </g>
                            <g style={styles[1]}>
                                <text x={notePositions[note].x}
                                      y={notePositions[note].y}
                                      fontSize="0.4rem"
                                >
                                    {displayText}
                                </text>
                            </g>
                        </a>
                    )})}
            </svg>
        )
    }

    return (
        getSvg()
    );
};

export default Doughnut;
