import {Button, MenuItem, Select, useTheme} from "@mui/material";
import {songs} from "../utils/Notes";
import React from "react";

const PlayPauseSelect = ({ startAudioContext, stopMelody, sequence, setSequence }) => {
    const theme = useTheme();
    return (
        <div>
            <div>
                <Button onClick={() => startAudioContext(sequence)}
                        variant="contained"
                        sx={{
                            marginRight: '15px',
                        }}
                >
                    Play
                </Button>
                <Button onClick={stopMelody} variant="text">
                    Pause
                </Button>
            </div>
            <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={sequence}
                label="Song"
                onChange={(e) => setSequence(e.target.value)}
                sx={{
                    height: 40,
                    width: 150,
                    margin: 'auto',
                    marginTop: '15px',
                    borderColor: theme.palette.primary.main,
                    color: theme.palette.primary.main,
                    "& .MuiSvgIcon-root": {
                        color: theme.palette.primary.main,
                    }
                }}
            >
                {Object.keys(songs).map((key, index) => (
                    <MenuItem key={index} value={key}>{key}</MenuItem>
                ))}
            </Select>
        </div>
    )
}

export default PlayPauseSelect;
